    const md = reactive({
        md1:"<font size=4>The VNEL takes an image as input with bounding boxes around objects, and then links them to entities in KB.</font>",
        md2:"<font size=4>Generally, each entity $e_i \\in E$ is often characterized by rich textual and visual descriptions, and each modality of the description can provide sufficient information for visual entity linking. To make the task more clearly presented, we further decompose the VNEL task into three sub-tasks(i.e., visual to visual entity linking (V2VEL), visual to textual entity linking (V2TEL), and visual to visual-textual entity linking (V2VTEL)) according to the type of description used in learning entity embeddings.</font>",
        md3:"<font size=4>V2VEL In the first place, only the visual description $V_{e_i}$ of the entity is used in the visual entity linking stage, which we denote as the V2VEL sub-task. The core of V2VEL is to match two visual objects. It is worth noting that entities in KB may contain more than one image. To simplify this, we take the first image of $e_i$ as $V_{e_i}$ and leave the multiple images per entity as the future work.</font>",
        md4:"<font size=4>V2TEL In the second place, only the textual description $T_{e_i}$ of the entity is used in the visual entity linking stage, which we denote as the V2TEL sub-task. The V2TEL task aims to evaluate the ability in image-text matching, central to cross-modal entity linking.</font>",
        md5:"<font size=4>V2VTEL Finally, both the visual description and the textual description $(V_{e_i}, T_{e_i})$ of the entity could be employed to link the visual mention, which we denote as the V2VTEL sub-task. The V2VTEL task could leverage both textual and visual modality to complement each other in linking visual mentions.</font>",
